.header {
    margin-top: 60px;
  }
  
  .headerTitles {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Lora", serif;
    color: #323331;
  }
  
  .headerTitleSm {
    position: absolute;
    top: 15%;
    font-size: 20px;
  }
  
  .headerTitleLg {
    position: absolute;
    top: 20%;
    font-size: 100px;
  }
  
  .headerImg {
    width: 100%;
    height: 450px;
    margin-top: 80px;
    object-fit: cover;
    opacity: 0.5;
    
  }
  .Stories{
    margin-left: 500px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: xx-large;
  }
  